import type {MemberFastenerClass} from "@swim/fastener";
import {Trait, TraitRef} from "@swim/model";
import {Feel} from "@swim/theme";
import {TraitViewRef} from "@swim/controller";
import {TextCellView, TextCellTrait, TextCellController} from "@swim/table";
import {Status, StatusVector, StatusTrait} from "@swim/domain";

/** @public */
export class TableGadgetTextCellController extends TextCellController {
  @TraitViewRef<TableGadgetTextCellController, TextCellTrait, TextCellView>({
    extends: true,
    didAttachTrait(cellTrait: TextCellTrait, targetTrait: Trait | null): void {
      this.owner.status.setTrait(cellTrait.getTrait(StatusTrait));
      TextCellController.cell.prototype.didAttachTrait.call(this, cellTrait, targetTrait);
    },
    willDetachTrait(cellTrait: TextCellTrait): void {
      TextCellController.cell.prototype.willDetachTrait.call(this, cellTrait);
      this.owner.status.setTrait(null);
    },
    initView(cellView: TextCellView): void {
      TextCellController.cell.prototype.initView.call(this, cellView);
      const statusTrait = this.owner.status.trait;
      if (statusTrait !== null) {
        cellView.modifyMood(Feel.default, [[Feel.primary, 1]]);
        this.owner.applyStatusVector(statusTrait.statusVector, cellView);
      }
    },
  })
  override readonly cell!: TraitViewRef<this, TextCellTrait, TextCellView>;
  static override readonly cell: MemberFastenerClass<TableGadgetTextCellController, "cell">;

  @TraitRef<TableGadgetTextCellController, StatusTrait>({
    type: StatusTrait,
    observes: true,
    initTrait(statusTrait: StatusTrait): void {
      const cellView = this.owner.cell.view;
      if (cellView !== null) {
        cellView.modifyMood(Feel.default, [[Feel.primary, 1]]);
        this.owner.applyStatusVector(statusTrait.statusVector, cellView);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      const cellView = this.owner.cell.view;
      if (cellView !== null) {
        this.owner.applyStatusVector(newStatusVector, cellView);
      }
    },
  })
  readonly status!: TraitRef<this, StatusTrait>;
  static readonly status: MemberFastenerClass<TableGadgetTextCellController, "status">;

  protected applyStatusVector(statusVector: StatusVector, cellView: TextCellView): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      cellView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      cellView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      cellView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      cellView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      cellView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }
}
