import {Affinity, MemberFastenerClass} from "@swim/fastener";
import {Trait, TraitRef} from "@swim/model";
import {Look, Feel} from "@swim/theme";
import {TraitViewRef} from "@swim/controller";
import {AreaPlotView, AreaPlotTrait, AreaPlotController} from "@swim/chart";
import {Status, StatusVector, StatusTrait} from "@swim/domain";

/** @public */
export class ChartGadgetAreaPlotController<X = unknown, Y = unknown> extends AreaPlotController<X, Y> {
  @TraitViewRef<ChartGadgetAreaPlotController<X, Y>, AreaPlotTrait<X, Y>, AreaPlotView<X, Y>>({
    extends: true,
    didAttachTrait(plotTrait: AreaPlotTrait<X, Y>, targetTrait: Trait | null): void {
      this.owner.status.setTrait(plotTrait.getTrait(StatusTrait));
      AreaPlotController.plot.prototype.didAttachTrait.call(this, plotTrait as AreaPlotTrait, targetTrait);
    },
    willDetachTrait(plotTrait: AreaPlotTrait<X, Y>): void {
      AreaPlotController.plot.prototype.willDetachTrait.call(this, plotTrait as AreaPlotTrait);
      this.owner.status.setTrait(null);
    },
    initView(plotView: AreaPlotView<X, Y>): void {
      AreaPlotController.plot.prototype.initView.call(this, plotView as AreaPlotView);
      const statusTrait = this.owner.status.trait;
      if (statusTrait !== null) {
        plotView.modifyMood(Feel.default, [[Feel.primary, 1]]);
        this.owner.applyStatusVector(statusTrait.statusVector, plotView);
      }
      plotView.fill.setLook(Look.accentColor, Affinity.Intrinsic);
    },
  })
  override readonly plot!: TraitViewRef<this, AreaPlotTrait<X, Y>, AreaPlotView<X, Y>>;
  static override readonly plot: MemberFastenerClass<ChartGadgetAreaPlotController, "plot">;

  @TraitRef<ChartGadgetAreaPlotController<X, Y>, StatusTrait>({
    type: StatusTrait,
    observes: true,
    initTrait(statusTrait: StatusTrait): void {
      const plotView = this.owner.plot.view;
      if (plotView !== null) {
        plotView.modifyMood(Feel.default, [[Feel.primary, 1]]);
        this.owner.applyStatusVector(statusTrait.statusVector, plotView);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      const plotView = this.owner.plot.view;
      if (plotView !== null) {
        this.owner.applyStatusVector(newStatusVector, plotView);
      }
    },
  })
  readonly status!: TraitRef<this, StatusTrait>;
  static readonly status: MemberFastenerClass<ChartGadgetAreaPlotController, "status">;

  protected applyStatusVector(statusVector: StatusVector, plotView: AreaPlotView<X, Y>): void {
    const alert = statusVector.get(Status.alert) || 0;
    const warning = statusVector.get(Status.warning) || 0;
    const inactive = statusVector.get(Status.inactive) || 0;
    if (alert !== 0) {
      plotView.modifyMood(Feel.default, [[Feel.warning, 1], [Feel.alert, alert]]);
    } else if (warning !== 0) {
      plotView.modifyMood(Feel.default, [[Feel.warning, warning], [Feel.alert, void 0]]);
    } else {
      plotView.modifyMood(Feel.default, [[Feel.warning, void 0], [Feel.alert, void 0]]);
    }
    if (inactive !== 0) {
      plotView.modifyMood(Feel.default, [[Feel.inactive, inactive]]);
    } else {
      plotView.modifyMood(Feel.default, [[Feel.inactive, void 0]]);
    }
  }
}
