import {Affinity, MemberFastenerClass} from "@swim/fastener";
import type {Uri} from "@swim/uri";
import {ModelRef, TraitModelType} from "@swim/model";
import {StatusTrait, EntityTrait, EntityGroup} from "@swim/domain";
import {PlaneGroup} from "./PlaneGroup";

/** @public */
export class PlaneEntity extends EntityTrait {
  constructor(hostUri: Uri) {
    super(hostUri);
  }

  protected override onAttachModel(model: TraitModelType<this>): void {
    super.onAttachModel(model);
    if (model.warpRef.hasAffinity(Affinity.Intrinsic)) {
      let warpRef = model.warpRef.superState;
      if (warpRef === void 0 || warpRef === null) {
        warpRef = model.warpProvider.service.client;
      }
      warpRef = warpRef.hostRef(this.uri);
      model.warpRef.setState(warpRef, Affinity.Intrinsic);
    }
    this.subentities.insertModel();
  }

  @ModelRef<PlaneEntity, EntityGroup>({
    extends: true,
    createModel(): EntityGroup {
      const entityGroup = new PlaneGroup();
      entityGroup.setTrait("status", new StatusTrait());
      return entityGroup;
    },
  })
  override readonly subentities!: ModelRef<this, EntityGroup>;
  static override readonly subentities: MemberFastenerClass<PlaneEntity, "subentities">;
}
